body{
    height: 100%;
    width: 100%;
    background: rgb(207,11,163);
background: linear-gradient(90deg, rgba(207,11,163,1) 29%, rgba(133,170,176,1) 65%);
}

h1{
    text-shadow: 1px 1px 2px red, 0 0 1em blue, 0 0 0.2em blue;
}

#input{
    width: 300px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

#forms{
    display: flex;
    flex-direction:  column;
    justify-content: center;
    align-items: center;
}
#btn{
    width: 100px;
    height: 40px;
    background-color: rgb(27, 206, 87);
    color: rgb(25, 27, 24);
    margin-top: 10px;
    margin-left: 20px;
}

#notes-list{
    background-color:darkgoldenrod;
    color: darkblue;
}

